.ministies{
    display: grid;
    justify-content: center;
    align-items: center;

    
.bottom-nav {
    display: block;
        height: 58px;
        box-shadow: 0 -2px 5px -2px #333;
        background-color:black;
        position: fixed;
        bottom: 0px;
        left: 0px;
        width: 100%;
        border-radius: 10px 10px 0px 0px;
        z-index: 3;
   
      .sticky-footer {
            display: block;
            height: 58px;
            border-radius: 10px 10px 0px 0px;
            box-shadow: 0 -2px 5px -2px #333;
            background-color:black;
            position: fixed;
            bottom: 0px;
            left: 0px;
            width: 100%;
            z-index: 3;
      }
      .sticky-footer .one-fourth {
            width: 20%;
            float: left;
            color: #fff;
            text-align: center;
            height: 58px;
            position: relative;
            cursor: pointer;
      }
      .sticky-search,
      .sticky-filters,
      .sticky-map,
      .sticky-radius {
            width: 25px;
            height: 25px;
            margin: auto;
            margin-top: 11px;
      }
      .sticky-footer .one-fourth p {
            font-size: 10px;
            margin-top: -2px;
      }
      
  @import url('https://fonts.googleapis.com/css?family=Poppins&display=swap');

      nav {
            border-radius: 10px 10px 0px 0px;
            width: calc(100%);
            position: absolute;
            z-index: 3;  
            bottom: 0;
      }

     
      .nav ul {
          list-style-type: none;
          padding: inherit;

      }
      
      .nav.vertical ul {
          flex-direction: column;
      }
      
      .nav ul li a{
          display: inline-block;
          color: #fff;
          transition: transform 0.3s cubic-bezier(0.925, 0.150, 0.505, 1.030);
      }
      
      .nav ul li a:hover{
          transform: scale(1.3);
      }
      
      nav::after {
          content: "";
          width: 100%;
          height: 100%;
          display: inline-block;
          border-radius: 35px;
          position: absolute;
          left: 0;
          top: 0;
              transform: scaleX(1.0);
          z-index: -1;
          transition: all 0.3s cubic-bezier(0.925, 0.150, 0.505, 1.030);
      }
      
      nav.horizontal:hover::after {
          transform: scaleX(1.1);
      }
      
      nav.vertical:hover::after {
          transform: scaleY(1.1);
      }
}

.img-ministries{
      width: calc(100%);
      height: auto;
      padding: 5px;
  }
}