.header-container{
    position: fixed;
    background: black;
    top: 0px;
    left: 0px;
    
    width:100%;
    height:50px;
    border-bottom-left-radius:10% 10%;
    border-bottom-right-radius:10% 10%;
    box-shadow: 0px 4px 10px 0px rgba(0,0,0,0.36);
    
    z-index: 3;
    display: grid;
    grid-template-columns: 30% 40% 30%;
    grid-template-rows: auto;
    grid-template-areas: 
    "a b c";

    .headertitle{
        grid-area: b;
        position: absolute;
        font-size: 18px;
        text-align: center;
        color: white;
        justify-content: center;
        align-self: center;
        position: relative;
    }

    .logout{
        grid-area: c;
        position: absolute;
        right: 16px;
        font-size: 42px;
    }

    .status{
        grid-area: a;
        position: absolute;
    }
    
}