.new-user-container{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;
    height: 100vh;
    background: #fff;

    .form {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        width: 100%;
        text-align: center;
        padding: 2em;
    }

    header {
        width: 220px;
        height: 220px;
        margin: 1em auto;
      }

    .form input {
        width: 100%;
        padding: 13px 15px;
        margin: 0.7em auto;
        border-radius: 100px;
        border: none;
        background: #333;
        font-family: 'Poppins', sans-serif;
        outline: none;
        color: #fff;
    }
    
    input::placeholder {
        color: #fff;
        font-size: 13px;
    }

    .inputs {
        margin-top: auto;
    }

    footer {
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        padding: 2em;
        text-align: center;
    }

    button {
        width: 100%;
        padding: 13px 15px;
        border-radius: 100px;
        border: none;
        background: #257aa6;
        font-family: 'Poppins', sans-serif;
        outline: none;
        color: #fff;
        margin-bottom: 60px;
    }
    
    @media screen and (max-width: 640px) {
            .app {
                width: 100%;
                height: 100vh;
                border-radius: 0;
            }

            .bg {
                top: -7em;
                width: calc(100%);
                height: 95vh;
            }
            header {
                width: 90%;
                height: 250px;
            }
            .inputs {
                margin: 0;
            }
            input, button {
                padding: 18px 15px;
                margin-bottom: 60px;

            }
        }
}