.login-container {
    @import url('https://fonts.googleapis.com/css?family=Playfair+Display:400,900|Poppins:400,500');
    * {
      margin: 0;
      padding: 0;
      text-decoration: none;
      box-sizing: border-box;
      -webkit-tap-highlight-color:rgba(0,0,0,0);
      -webkit-text-size-adjust:none;
      -webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale;
    }
 

    img {
        max-width: 100%;
    }

    .app {
      background-color: #fff;
      width: 330px;
      height: 570px;
      margin: 2em auto;
      border-radius: 5px;
      padding: 1em;
      position: relative;
      overflow: hidden;
      box-shadow: 0 6px 31px -2px rgba(0, 0, 0, .3);
    }

    a {
        text-decoration: none;
        color: #257aa6;
    }

    p {
        font-size: 13px;
        color: #333;
        line-height: 2;
    }
        .light {
            text-align: right;
            color: #fff;
        }
            .light a {
                color: #fff;
            }

    .bg {
        width: calc(100%);
        height: 75%;
        background: white;// #257aa6;
        position: absolute;
        top: -5em;
        left: 0;
        right: 0;
        margin: auto;
       // background-image: url("../../images/deco.jpg");
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
        -webkit-clip-path: ellipse(69% 46% at 48% 46%);
        clip-path: ellipse(69% 46% at 48% 46%);
    }

    .form {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        width: 100%;
        text-align: center;
        padding: 2em;
    }

    header {
        width: 220px;
        height: 220px;
        margin: 1em auto;
      }

    .form input {
        width: 100%;
        padding: 13px 15px;
        margin: 0.7em auto;
        border-radius: 100px;
        border: none;
        background: #333;
        font-family: 'Poppins', sans-serif;
        outline: none;
        color: #fff;
    }
    
    input::placeholder {
        color: #fff;
        font-size: 13px;
    }

    .inputs {
        margin-top: auto;
    }

    footer {
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        padding: 2em;
        text-align: center;
    }

    button {
        width: 100%;
        padding: 13px 15px;
        border-radius: 100px;
        border: none;
        background: #257aa6;
        font-family: 'Poppins', sans-serif;
        outline: none;
        color: #fff;
    }
    
    @media screen and (max-width: 640px) {
            .app {
                width: 100%;
                height: 100vh;
                border-radius: 0;
            }

            .bg {
                top: -7em;
                width: calc(100%);
                height: 95vh;
            }
            header {
                width: 90%;
                height: 250px;
            }
            .inputs {
                margin: 0;
            }
            input, button {
                padding: 18px 15px;
            }
        }
}
