.sermon{
  display: contents;
  align-items: center;
  justify-content: center;
  width: calc(100%);
  height: 100vh;

  .bottom-nav {
      .tab-nav-container {
            background-color: #fff;
            border-top-right-radius: 30px;
            border-top-left-radius: 30px;
            display: inline-flex;
            justify-content: space-evenly;
            height: 58px;
            box-shadow: 0 -2px 5px -2px rgb(81, 118, 129);
            position: fixed;
            bottom: 0px;
            left: 0px;
            width: 100%;
            z-index: 3;
            transition: background 0.4s linear;
            text-align: center;
            margin: 0;
        }
        
        .tab {
            background-color: #ffffff;
            border-radius: 50px;
            cursor: pointer;
            display: flex;
            align-items: center;
            justify-content: center;
            padding: 0 20px;
            margin: 0 10px;
            transition: background 0.4s linear;
        }
        
        .tab i {
            font-size: 1.2em;
        }
        
        .tab p {
            font-weight: bold;
            overflow: hidden;
            max-width: 0;
        }
        
        .tab.active p {
            margin-left: 10px;
            max-width: 200px;
            transition: max-width 0.4s linear;
        }
        
        .tab.active.purple {
            //width: 50%;
           // background-color: rgba(91, 55, 183, 0.2);
          //  color: rgba(91, 55, 183, 1);
        }
        
        .tab.active.pink {
            //width: 50%;
            //background-color: rgba(201, 55, 157, 0.2);
          //  color: rgba(201, 55, 157, 1);
        }
        
        @media (max-width: 450px) {
            .tab-nav-container {
                padding: 20px;
            }
        
            .tab {
                padding: 0 10px;
                margin: 0;
            }
        
            .tab i {
                font-size: 1em;
            }
        }
        
      background-color: transparent;
      }

      .htNdhd {
            position: relative;
            margin: 0 auto;
            border-radius: 5px;
            box-shadow: 0px 0px 15px -5px rgba(107, 166, 206, 0.75);
      }

      .kHWrPi {
            border-radius: 5px 5px 0 0;
            height: 240px;
            padding: 2rem;
            display: -webkit-box;
            display: -webkit-flex;
            display: -ms-flexbox;
            display: flex;
            -webkit-flex-wrap: wrap;
            -ms-flex-wrap: wrap;
            flex-wrap: wrap;
            color: #27117c;
            box-sizing: border-box;
            background-image: linear-gradient( to bottom, #ebe6f8, #ece5f6, #ede4f5, #eee4f3, #efe3f1 );
      }

      .cgLFdP {
            height: 260px;
            box-sizing: border-box;
            overflow: scroll;
      }

      .gkcZCz {
            /* width: 100%; */
            height: 45px;
            cursor: pointer;
            color: #b2b2b2;
            margin-left: 2rem;
            margin-top: 1rem;
            display: -webkit-box;
            display: -webkit-flex;
            display: -ms-flexbox;
            display: flex;
            box-sizing: border-box;
      }

      //////////@extend
      $finalHeight: 250px;
      $finalWidth: 3 * $finalHeight;
      $scrollBarHeight: 1px;
      
      ::-webkit-scrollbar {
        width: $scrollBarHeight;
        height: $scrollBarHeight;
      }
      
      ::-webkit-scrollbar-button {
        width: $scrollBarHeight;
        height: $scrollBarHeight;
      }
      
  
      div {
        box-sizing: border-box;
      }
      
      .horizontal-scroll-wrapper {
        position: absolute;
        display: block;
        top: 0;
        left: 0;
        width: calc(#{$finalHeight} + #{$scrollBarHeight});
        max-height: $finalWidth;
        margin: 0;
        padding-top: $scrollBarHeight;
        background: #abc;
        overflow-y: auto;
        overflow-x: hidden;
        transform: rotate(-90deg) translateY(-$finalHeight);
        transform-origin: right top;
        & > div {
          display: block;
          padding: 5px;
          background: #cab;
          transform: rotate(90deg);
          transform-origin: right top;
        }
      }
      
      .squares {
        padding: $finalHeight 0 0 0;
        & > div {
          width: $finalHeight;
          height: $finalHeight;
          margin: 10px 0;
        }
      }
}