.profile-container{
    @import url("https://fonts.googleapis.com/css?family=Montserrat:300,400,700");
    * {
      box-sizing: border-box;
    }
    
    html {
      box-sizing: border-box;
      font-size: 14px;
    }
    
    *,
    *:before,
    *:after {
      box-sizing: inherit;
    }
    
    ::-webkit-input-placeholder {
      color: #e4e4e4;
    }
    
    body {
      width: 100%;
      height: 100%;
      margin: 0;
      padding: 0;
      font-family: "Montserrat", sans-serif;
      background-color: #222;
    }
    
    .phone-wrapper {
      position: relative;
      width: 330px;
      height: 550px;
      margin: 40px auto;
      -webkit-user-select: none;
         -moz-user-select: none;
          -ms-user-select: none;
              user-select: none;
      border-radius: 5px;
      background-color: #fff;
      color: #444;
      text-align: center;
      font-size: 1.1rem;
      overflow: hidden;
    }
    
    .background-image-wrapper {
      position: absolute;
      top: 0;
      width: 100%;
      height: 100%;
      background-image: url("../../images/ProfilePic.png");
      background-size: contain;
      background-repeat: no-repeat;
      -webkit-transition: -webkit-transform 0.5s ease-in-out 0.2s;
      transition: -webkit-transform 0.5s ease-in-out 0.2s;
      transition: transform 0.5s ease-in-out 0.2s;
      transition: transform 0.5s ease-in-out 0.2s, -webkit-transform 0.5s ease-in-out 0.2s;
      -webkit-transform: scale(1);
              transform: scale(1);
    }
    .background-image-wrapper.open {
      -webkit-transform: scale(1.1) translatey(-5px);
              transform: scale(1.1) translatey(-5px);
    }
    
    .details-wrapper {
      position: absolute;
      bottom: 0;
      border: 1px solid #ddd;
      border-radius: 5px;
      display: -webkit-box;
      display: flex;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
              flex-direction: column;
      -webkit-box-pack: justify;
              justify-content: space-between;
      background-color: #fff;
      -webkit-transition: -webkit-transform 0.8s ease-in-out;
      transition: -webkit-transform 0.8s ease-in-out;
      transition: transform 0.8s ease-in-out;
      transition: transform 0.8s ease-in-out, -webkit-transform 0.8s ease-in-out;
      -webkit-transform: scale(0.9) translatey(-15px);
              transform: scale(0.9) translatey(-15px);
    }
    .details-wrapper.open {
      -webkit-transform: scale(1) translatey(0);
              transform: scale(1) translatey(0);
    }
    .details-wrapper.open .name-label::after {
      -webkit-transform: rotate(-180deg);
              transform: rotate(-180deg);
    }
    .details-wrapper.open .person-details {
      height: 150px;
      overflow: hidden;
    }
    .details-wrapper.open .description,
    .details-wrapper.open .email,
    .details-wrapper.open .phone,
    .details-wrapper.open .province,
    .details-wrapper.open .branch,
    .details-wrapper.open .designation
    .details-wrapper.open .username  {
      opacity: 1;
      -webkit-transform: translatey(0);
              transform: translatey(0);
      -webkit-transition-delay: 0.4s;
              transition-delay: 0.4s;
    }
    .details-wrapper.open .phone {
      -webkit-transition-delay: 0.5s;
              transition-delay: 0.5s;
    }
    .details-wrapper.open .email {
      -webkit-transition-delay: 0.5s;
              transition-delay: 0.5s;
    }

    .details-wrapper.open .province {
        -webkit-transition-delay: 0.5s;
                transition-delay: 0.5s;
      }

      .details-wrapper.open .branch {
        -webkit-transition-delay: 0.5s;
                transition-delay: 0.5s;
      }

      .details-wrapper.open .designation {
        -webkit-transition-delay: 0.5s;
                transition-delay: 0.5s;
      }

      .details-wrapper.open .username {
        -webkit-transition-delay: 0.5s;
                transition-delay: 0.5s;
      }
     
    
    .names-wrapper .name-label {
      display: -webkit-box;
      display: flex;
      -webkit-box-pack: justify;
              justify-content: space-between;
      text-transform: uppercase;
      cursor: pointer;
      padding: 20px 20px 10px;
      font-weight: 700;
      font-size: 1.2em;
    }
    .names-wrapper .name-label:after {
      font-family: FontAwesome;
      content: "\f0d8";
      -webkit-transition: -webkit-transform 0.8s ease-in-out 0.3s;
      transition: -webkit-transform 0.8s ease-in-out 0.3s;
      transition: transform 0.8s ease-in-out 0.3s;
      transition: transform 0.8s ease-in-out 0.3s, -webkit-transform 0.8s ease-in-out 0.3s;
      -webkit-transform-origin: center;
              transform-origin: center;
    }
    
    .person-details {
      text-align: left;
      height: 0;
      overflow: hidden;
      -webkit-transition: height 0.7s ease-in-out;
      transition: height 0.7s ease-in-out;
      will-change: height;
      font-size: 0.9em;
    }
    .person-details .description,
    .person-details .email,
    .person-details .phone 
    .person-details .province,
    .person-details .branch,
    .person-details .designation
    .person-details .username {
      opacity: 0;
      -webkit-transform: translatey(20px);
              transform: translatey(20px);
      -webkit-transition: opacity 0.4s ease-in-out, -webkit-transform 0.6s ease-in-out;
      transition: opacity 0.4s ease-in-out, -webkit-transform 0.6s ease-in-out;
      transition: transform 0.6s ease-in-out, opacity 0.4s ease-in-out;
      transition: transform 0.6s ease-in-out, opacity 0.4s ease-in-out, -webkit-transform 0.6s ease-in-out;
      -webkit-transition-delay: 0;
              transition-delay: 0;
    }
    .person-details .description {
      padding: 5px 20px;
      text-justify: justify;
    }
    .person-details .email,
    .person-details .phone, 
    .person-details .province, 
    .person-details .branch, 
    .person-details .designation, 
    .person-details .username {
      display: -webkit-box;
      display: flex;
      -webkit-box-align: center;
              align-items: center;
      padding: 5px 20px;
    }
    .person-details .email::before,
    .person-details .phone::before 
    .person-details .province::before,
    .person-details .branch::before 
    .person-details .designation::before,
    .person-details .username::before {
      font-family: FontAwesome;
      content: "\f10b";
      margin-right: 10px;
    }
    .person-details .phone::before {
      content: "\f10b";
      font-size: 1.5em;
    }
    .person-details .email::before {
      content: "\f0e0";
    }
    
    .stats-wrapper {
      padding: 20px;
      display: -webkit-box;
      display: flex;
      -webkit-box-pack: justify;
              justify-content: space-between;
      -webkit-box-align: center;
              align-items: center;
    }
    
    .stats {
      text-align: left;
    }
    .stats .stats-count {
      font-size: 1.7em;
      font-weight: 400;
    }
    .stats .label {
      font-size: 0.9rem;
      font-weight: 200;
      text-transform: uppercase;
    }
    
    .footer-note {
      color: #fff;
    }
    .footer-note .ajduke {
      text-align: center;
    }
    .footer-note .ajduke .fa-heart {
      color: tomato;
      padding: 0 5px;
    }
    .footer-note .ajduke a {
      text-decoration: none;
      border-bottom: 1px dotted;
    }
    .footer-note .ajduke a:hover, .footer-note .ajduke a:visited, .footer-note .ajduke a.active {
      color: inherit;
    }
}